import React from 'react';
import ReactDOM from 'react-dom';
import { Provider, connect } from 'react-redux';
import {createStore} from 'redux';

const rootElement = document.getElementById("root");
//React
const Presentational = (props) => {
  return (
    <QuoteBox state={props}/>
  );
};

class QuoteBox extends React.Component {
  constructor(props) {
    super(props);
    this.newQuote = this.newQuote.bind(this);
  }

  newQuote() {
    let randomQuote = Math.floor(Math.random() * QUOTES.length);
    this.props.state.generateNewQuote(randomQuote);
  }

  render() {
    let randomColor = COLORS[Math.floor(Math.random() * COLORS.length)];
    document.body.style.backgroundColor = 'rgb(' + randomColor.r + ', ' + randomColor.g + ', ' + randomColor.b + ')';
    return (
      <div id="quote-box">
        <TextArea state={this.props.state}/>
        <ButtonArea newQuote={this.newQuote}/>
      </div>
    );
  }
};

const TextArea = (props) => {
  const currentQuote = props.state.currentQuote;
  return (
    <div className="Quote">
      <div id="text">
        <p>{currentQuote.quote}</p>
      </div>
      <div id="author">
        <blockquote> - {currentQuote.author}</blockquote>
      </div>
    </div>
  );
};

const ButtonArea = (props) => {
    return (
      <div className="Buttons">
        <a 
          id="tweet-quote"
          target="_blank"
          rel="noopener noreferrer"
          href="https://twitter.com/intent/tweet"
          >
          <button id="twitter">
            <i className="fa fa-twitter"></i>
          </button>
        </a>
        <button id="new-quote"
          onClick={props.newQuote}>
            Quote Machine
          </button>
      </div>
    );
};


const QUOTES = [
  {
    quote: "Every true man, sir, who is a little above the level of the beasts and plants, lives so as to give a meaning and a value to his own life.",
    author: "Luigi Pirandello"
  },
  {
    quote: "Never let go of that fiery sadness called desire.",
    author: "Patti Smith"
  },
  {
    quote: "For every man there exists a bait which he cannot resist swallowing.",
    author: "Friedrich Nietzsche"
  },
  {
    quote: "The moment somebody says to me, 'This is very risky,' is the moment it becomes attractive to me.",
    author: "Kate Capshaw"
  },
  {
    quote: "Discontent is the first step in progress. No one knows what is in him till he tries, and many would never try if they were not forced to.",
    author: "Basil W. Maturin"
  },
  {
    quote: "All progress is based upon a universal, innate desire on the part of every living organism to live beyond its income.",
    author: "Samuel Butler"
  },
  {
    quote: "It is the spur of ignorance, the consciousness of not understanding, and the curiosity about that which lies beyond that are essential to our progress",
    author: "John Pierce"
  },
  {
    quote: "One starts an action simply because one must do something.",
    author: "T. S. Eliot"
  },
  {
    quote: "What you are must always displease you, if you would attain to that which you are not.",
    author: "Saint Augustine"
  },
  {
    quote: "If you're good to your staff when things are going well, they'll rally when times go bad.",
    author: "Mary Kay Ash"
  }
]
const COLORS = [
  {name: "medium purple", r: 147, g: 112, b: 219},
  {name: "royal blue", r: 65, g: 105, b: 225},
  {name: "light sky blue", r: 135, g: 206, b: 250},
  {name: "orchid", r: 218, g: 112, b: 214},
  {name: "aqua marine", r: 127, g: 255, b: 212},
  {name: "teal", r: 0, g: 128, b: 128},
  {name: "medium sea green", r: 60, g: 179, b: 113},
  {name: "lime green", r: 50, g: 205, b: 50},
  {name: "khaki", r: 240, g: 230, b: 140},
  {name: "golden rod", r: 218, g: 165, b: 32},
  {name: "orange red", r: 255, g: 69, b: 0},
  {name: "firebrick", r: 178, g: 34, b: 34}
]
//Redux
const NEW = 'NEW';
const initialState = QUOTES[Math.floor(Math.random() * QUOTES.length)];

const newQuote = (i) => {
  return {
    type: NEW,
    index: i
  };
}

const mapStateToProps = (quote) => {
  return {
    currentQuote: quote
  };
}

const MapDispatchToProps = (dispatch) => {
  return {
    generateNewQuote: (i) => {
      dispatch(newQuote(i));
    }
  };
}

const reducer = (state = initialState, action) => {
  switch(action.type) {
    case(NEW):
      return QUOTES[action.index];
    default:
      return state;
  }
}

const store = createStore(reducer);
const Container = connect(mapStateToProps,MapDispatchToProps)(Presentational);

class Wrapper extends React.Component {
  render() {
    return (
    <Provider store={store}>
      <Container />
    </Provider>
    );
  }
};

ReactDOM.render(<Wrapper/>, rootElement);